export default {
	data() {
		return {
			optionH: 0,
			showbtntop: false,
			list: [],
			showlist: [],
			showOption: -1,
			options: [{
					text: "价格",
					textclass: '',
					classname: "a",
					action: 'changePrice',
					changeNum: 0
				},
				{
					text: "折扣",
					textclass: '',
					classname: "b",
					action: 'changeHaircut',
					changeNum: 0
				},
				{
					text: "品牌",
					classname: "c",
					textclass: 'td',
					action: '',
					changeNum: 0
				},
				{
					text: "筛选",
					classname: "d",
					textclass: 'td',
					action: '',
					changeNum: 0
				},
			]
		};
	},
	methods: {
		action(action, index) {
			uni.showLoading({
				title: ''
			});
			
			uni.pageScrollTo({
				scrollTop: 0
			});
			
			switch (action) {
				case 'changePrice':
					this.action_logic(action, index,'price_1')
					break;
				case 'changeHaircut':
					this.action_logic(action, index,'price_3')
					break;
				default:
					break;
			}
			setTimeout( ()=> {
				uni.hideLoading();
			}, 100);
			
		},
		action_logic(action,index,price){
			this.showOption = index
			this.options = this.options.map(i => {
				i.action === action ? i.changeNum++ : i.changeNum = 0
				return i
			})
			let num = this.options[index].changeNum % 3
			switch (num) {
				case 0:
					this.onrequest()
				break;
				case 1:
					this.list = this.list.sort((a,b)=>{
						return Number(a[price])-Number(b[price])
					})
				break;
				case 2:
					this.list = this.list.sort((a,b)=>{
						return Number(b[price])-Number(a[price])
					})
				break;
				default:
					break;
			}
			this.showlist = this.list.slice(0, 6)
		},
		onrequest(){
			uni.request({
				url: 'https://www.easy-mock.com/mock/5cb03f83f593356878131486/wph/shop',
				success: (res) => {
					this.list = res.data.list
					this.showlist = this.list.slice(0, 6)
				}
			})
		}
	},
	onPageScroll(obj) {
		if (obj.scrollTop > 420) {
			this.showbtntop = true
		} else {
			this.showbtntop = false
		}
	},
	onReachBottom() {
		let bl = this.list.length > this.showlist.length
		if (bl) {
			uni.showLoading({
				title: ''
			});

			setTimeout(() => {
				this.showlist = this.list.slice(0, 8)
				uni.hideLoading();
			}, 100);
		} else {
			uni.showToast({
				title: "没有了",
				duration: 1000,
				icon: "none"
			});
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				console.log(res.statusBarHeight);
				this.optionH = `top: ${res.statusBarHeight + 46 + 60}rpx`
			}
		})
		this.onrequest()
	}
}
